import { usePress } from '#hooks/UNSAFE_usePress';
import { ViewHideIcon } from '#PRIVATE_ThemedIcons/ViewHideIcon';
import { ViewIcon } from '#PRIVATE_ThemedIcons/ViewIcon';
import { classNames } from '#utils/UNSAFE_classNames';
import { useComponentTheme } from '#hooks/UNSAFE_useComponentTheme';
import { ButtonLayoutRedwoodTheme } from '#UNSAFE_ButtonLayout/themes/redwood/ButtonLayoutTheme';
import { revealToggleIconBaseStyles } from './RevealToggleIconStyles.css';
import { HTMLAttributesSignalExcluded } from '#utils/UNSAFE_attributeUtils';
import { useTabbableMode } from '#hooks/UNSAFE_useTabbableMode';

type RevealToggleIconProps = {
  onPress: (event: Event) => void;
  isRevealed?: boolean;
  'aria-label': HTMLAttributesSignalExcluded['aria-label'];
};

export function RevealToggleIcon({
  isRevealed,
  onPress,
  'aria-label': ariaLabel
}: RevealToggleIconProps) {
  const { pressProps } = usePress(onPress);
  const { tabbableModeProps } = useTabbableMode();

  // Because the RevealToggleIconStyles.css.ts uses buttonLayoutBaseTheme styles, we
  // need their vars to be loaded as well. This is why we are adding the buttonLayoutBaseTheme
  // style class.
  const { baseTheme: buttonLayoutBaseTheme } = useComponentTheme(ButtonLayoutRedwoodTheme);
  const styleClasses = classNames([buttonLayoutBaseTheme, revealToggleIconBaseStyles]);

  // TODO: JET-51517 implement reveal icon title as a Tooltip
  // TODO: JET-49207 use toggle <Button> when that is available
  return (
    <button
      aria-label={ariaLabel}
      role="switch"
      aria-checked={isRevealed ? false : true}
      class={styleClasses}
      {...tabbableModeProps}
      {...pressProps}>
      {isRevealed ? <ViewHideIcon /> : <ViewIcon />}
    </button>
  );
}
